  <p>
    While it is very common still among music producers, beat makers and so on
    to use corporate and oftentimes expensive software to make music at home on
    the computer (or in the studio), there's also a plethora of free and
    open-source software tools that allow users to do music with a minimal (or
    zero) budget and get results that are just as exciting and inspiring as what
    you'd get by using popular applications like Ableton Live, Serum etc. One of
    the great things about engaging one with these open-source tools is that you
    will quickly be introduced to a vast <em>community</em> of people that work on
